
import { Container } from "react-bootstrap";
import CardHome from "../components/CardHome";
import Filter from "../components/Filter";
import Search from "../components/Search";
import Slider from "../components/Slider";





const Home =()=>{


    const app = process.env.REACT_APP_SERVER_URL
    const server = process.env.REACT_APP_SURBSCRIPTION_URL

    return (

        <Container fluid>
            <p>{app}</p>
            <p>{server}</p>
            <Search/>
            <Slider/>
            <Filter/>
            <CardHome/>
            <CardHome/>
        </Container>
    )
}



export default Home;